import Reacy from "react";
import "./Slider2.scss";
import leftArrow from "./left-arrow.svg";
import rightArrow from "./right-arrow.svg";

export default function BtnSlider({ direction, moveSlide }) {
  console.log(direction, moveSlide);
  return (
    <button
      className={direction === "next-home" ? "btn-slide-2-home next-home" : "btn-slide-2-home prev-home"}
      onClick={moveSlide}
    >
        
      <img src={direction === "next-home" ? rightArrow : leftArrow} />
    </button>
  );
}
