import React, { useState } from "react";
// import "./Slider2.scss";
import { Link } from "react-router-dom";

import BtnSlider from "./BtnSlider";
import dataSlider from "./dataSlider";

export default function Slider() {
  const [slideIndex, setSlideIndex] = useState(1);
  const nextSlide = () => {
    if (slideIndex !== dataSlider.length) {
      setSlideIndex(slideIndex + 1);
    } else if (slideIndex === dataSlider.length) {
      setSlideIndex(1);
    }
  };
  const prevSlide = () => {
    if (slideIndex !== 1) {
      setSlideIndex(slideIndex - 1);
    } else if (slideIndex === 1) {
      setSlideIndex(dataSlider.length);
    }
  };
  const changeSlide = () => {
    if (slideIndex !== dataSlider.length) {
      setSlideIndex(slideIndex + 1);
    } else if (slideIndex === dataSlider.length) {
      setSlideIndex(1);
    }
  };
  const moveDot = (index) => {
    setSlideIndex(index);
  };
  return (
    <div className="container-slider">
      {dataSlider.map((obj, index) => {
        return (
          <div
            key={obj.id}
            className={
              slideIndex === index + 1
                ? "slider__inside__container active-anim"
                : "slider__inside__container"
            }
          >
            <div className="slider__image-container">
              <img
                src={obj.pic}
                className={
                  slideIndex === index + 1 ? "slide active-anim initi" : "slide"
                }
              />
            </div>
            <div className="slide__content-2-2">
            <div className="slider__content-2">
              <div>
                <p className="Name">{obj.Name}</p>
                <p className="Para">{obj.Para}</p>
                <a href={obj.Link} className="btn-slider-more">
                  Read More
                </a>
                <Link to='/blog/artists' className="huehue">{obj.huehue}</Link>
              </div>
            </div>
            </div>
          </div>
        );
      })}
      <BtnSlider moveSlide={nextSlide} direction={"next"} />
      <BtnSlider moveSlide={prevSlide} direction={"prev"} />

      <div className="container-dots">
        {Array.from({ length: 6 }).map((item, index) => (
          <div
            onClick={() => {
              moveDot(index + 1);
            }}
            className={slideIndex === index + 1 ? "dot active" : "dot"}
          ></div>
        ))}
      </div>
    </div>
  );
}
