import React, { useState } from "react";
import "./Slider2.scss";
import { Link } from "react-router-dom";

import BtnSlider from "./BtnSlider";
import dataSlider from "./dataSlider";

export default function Slider() {
  const [slideIndex, setSlideIndex] = useState(1);
  const nextSlide = () => {
    if (slideIndex !== dataSlider.length) {
      setSlideIndex(slideIndex + 1);
    } else if (slideIndex === dataSlider.length) {
      setSlideIndex(1);
    }
  };
  const prevSlide = () => {
    if (slideIndex !== 1) {
      setSlideIndex(slideIndex - 1);
    } else if (slideIndex === 1) {
      setSlideIndex(dataSlider.length);
    }
  };
  const changeSlide = () => {
    if (slideIndex !== dataSlider.length) {
      setSlideIndex(slideIndex + 1);
    } else if (slideIndex === dataSlider.length) {
      setSlideIndex(1);
    }
  };
  const moveDot = (index) => {
    setSlideIndex(index);
  };
  return (
    <div className="slider-2-container">
      <div className="container-slider-2">
        {dataSlider.map((obj, index) => {
          return (
            <div
              key={obj.id}
              className={
                slideIndex === index + 1
                  ? "slider__inside__container-2 active-anim-2"
                  : "slider__inside__container-2"
              }
            >
              {/* <div className="slider__image-container-2"> */}
                <img
                  src={obj.pic}
                  className={
                    slideIndex === index + 1 ? "slide-2 active-anim-2" : "slide-2"
                  }
                />
              {/* </div> */}
              {/* <div className="slider__content-2"> */}
                <div className="slider-2-content-inside">
                  <p className="Para-2">{obj.Para}</p>
                  <p className="Para-2">{obj.Para2}</p>
                  <Link to={obj.Link} className="btn-slider-more-2">
                    {obj.LinkName}
                  </Link>
                </div>
              {/* </div> */}
            </div>
          );
        })}
        <BtnSlider moveSlide={prevSlide} direction={"prev-home"} />
        <BtnSlider moveSlide={nextSlide} direction={"next-home"} />

        {/* <div className="container-dots-2">
        {Array.from({ length: 3 }).map((item, index) => (
          <div
          onClick={() => {
            moveDot(index + 1);
          }}
          className={slideIndex === index + 1 ? "dot-2 active-2" : "dot-2"}
          ></div>
          ))}
        </div> */}
      </div>
    </div>
  );
}
