import React from "react";
import { Link } from "react-router-dom";
import "./contact-us.styles.scss";
import { useState } from "react";
import { send } from "emailjs-com";

import Header from "../../components/header/header.component";
import Footer from "../../components/footer-2/footer.component";

function ContactUsPage() {
  const [toSend, setToSend] = useState({
    from_name: "",
    email: "",
    phone: "",
    city: "",
    message: "",
  });

  const onSubmit = (e) => {
    e.preventDefault();
    // send("service_s0isucx", "template_Ichhglc", toSend, "-MmAci8sg581Dqsyw")
    send("service_lf8rgfb", "template_8khipbb", toSend, "5DSWl0z7HTNIy1VXq")
      .then((response) => {
        console.log("SUCCESS!", response.status, response.text);
      })
      .catch((err) => {
        console.log("FAILED...", err);
      });
    const arr = document.querySelectorAll(".input-text");
    arr.forEach((e) => {
      e.value = "";
    });
  };

  const handleChange = (e) => {
    setToSend({ ...toSend, [e.target.name]: e.target.value });
  };
  return (
    <div>
      <div className="contact-us">
        <div className="contact-us-1cont">
          <Header />
          <section className="get_in_touch">
            <form onSubmit={onSubmit}>
              <h1 className="title">share your story</h1>

              <div className="container">
                <div className="contact-form row">
                  <div className="form-field col-lg-6">
                    <input
                      id="for_name"
                      className="input-text"
                      type="text"
                      name="from_name"
                      value={toSend.from_name}
                      onChange={handleChange}
                    />
                    <label htmlFor="name" className="label">
                      name *
                    </label>
                  </div>
                  <div className="form-field col-lg-6">
                    <input
                      id="email"
                      className="input-text"
                      type="email"
                      name="email"
                      value={toSend.email}
                      onChange={handleChange}
                    />
                    <label htmlFor="email" className="label">
                      email *
                    </label>
                  </div>
                  <div className="form-field col-lg-6">
                    <input
                      id="city"
                      className="input-text"
                      type="city/country"
                      name="city"
                      value={toSend.city}
                      onChange={handleChange}
                    />
                    <label htmlFor="city/country" className="label">
                      city / country *
                    </label>
                  </div>
                  <div className="form-field col-lg-6">
                    <input
                      id="phone"
                      className="input-text"
                      type="text"
                      name="phone"
                      value={toSend.phone}
                      onChange={handleChange}
                    />
                    <label htmlFor="phone" className="label">
                      contact number
                    </label>
                  </div>
                  <div className="form-field col-lg-12">
                    <input
                      id="message"
                      className="input-text"
                      type="text"
                      name="message"
                      value={toSend.message}
                      onChange={handleChange}
                    />
                    <label htmlFor="message" className="label">
                      message ( tell us your story ) *
                    </label>
                  </div>
                  <div className="form-field col-lg-12">
                    <input
                      className="submit-btn"
                      type="submit"
                      name=""
                      value="submit"
                    />
                  </div>
                </div>
              </div>
            </form>
          </section>
        </div>
        <div className="contact-page2">
          <a href="https://forms.gle/WuqjyR8LNM8Wsozx9" className="join-team"><h1>JOIN OUR TEAM</h1></a>

        </div>
      </div>
    </div>
  );
}
export default ContactUsPage;

