import "./App.css";
import { Switch, Route, Redirect } from "react-router-dom";

import Header from "./components/header/header.component";
import Footer from "./components/footer-2/footer.component";

import HomePage from "./pages/home-page/home-page2.component";

import AboutUsPage from "./pages/about-us-page/about-us.component";

import BlogPage from "./pages/blog-page/blog.component";

import ContactUsPage from "./pages/contact-us-page/contact-us.component";

// Stories City wise
import Mumbai from "./pages/home-page/slidersforcities/Mumbai/slider";
import Delhi from "./pages/home-page/slidersforcities/Delhi/slider";
import Bangalore from "./pages/home-page/slidersforcities/Bangalore/slider";
import Gurgaon from "./pages/home-page/slidersforcities/Gurgaon/slider";
import Hyderabad from "./pages/home-page/slidersforcities/Hyderabad/slider";
import Imphal from "./pages/home-page/slidersforcities/Imphal/slider";
import Jammu from "./pages/home-page/slidersforcities/Jammu/slider";
import Ahmedabad from "./pages/home-page/slidersforcities/Ahmedabad/slider";
import Assam from "./pages/home-page/slidersforcities/Assam/slider";
import Chandigarh from "./pages/home-page/slidersforcities/Chandigarh/slider";
import Kurukshetra from "./pages/home-page/slidersforcities/Kurukshetra/slider";
import Pusegaon from "./pages/home-page/slidersforcities/Pusegaon/slider";
import thane from "./pages/home-page/slidersforcities/Thane/slider";

//Stories further in blog
import Artists from './pages/blog-page/Artists-2/slider';
import Changemakers from './pages/blog-page/Changemakers-2/slider'

function App() {
  return (
    <div className="App">
      {/* <Header /> */}
      <Switch>
        {/* Home */}
        <Redirect exact from="/" to="/home" />
        <Route exact path="/home" component={HomePage} />
        <Route exact path="/about-us" component={AboutUsPage} />
        <Route exact path="/blog" component={BlogPage} />
        <Route exact path="/contact-us" component={ContactUsPage} />

        {/* City Wise Distribution of Stories */}
        <Route exact path="/mumbai-stories" component={Mumbai} />
        <Route exact path="/delhi-stories" component={Delhi} />
        <Route exact path="/bangalore-stories" component={Bangalore} />
        <Route exact path="/gurgaon-stories" component={Gurgaon} />
        <Route exact path="/jammu-stories" component={Jammu} />
        <Route exact path="/thane-stories" component={thane} />
        <Route exact path="/pusegaon-stories" component={Pusegaon} />
        <Route exact path="/hyderabad-stories" component={Hyderabad} />
        <Route exact path="/imphal-stories" component={Imphal} />
        <Route exact path="/ahmedabad-stories" component={Ahmedabad} />
        <Route exact path="/kurukshetra-stories" component={Kurukshetra} />
        <Route exact path="/chandigarh-stories" component={Chandigarh} />
        <Route exact path="/assam-stories" component={Assam} />

        {/* Blog further Stories After Read More */}
        <Route exact path="/blog/artists" component={Artists} />
        <Route exact path="/blog/changemakers" component={Changemakers} />


      </Switch>
      <Footer />

    </div>
  );
}

export default App;
