import React from 'react';
import './footer.styles.scss';
import InstaIcon from './insta icon.png';
import TwitterIcon from './twitter icon.png';
import FacebookIcon from './facebook icon.png';
import YoutubeIcon from './youtube-icon.png';
import LinkedinIcon from './linkedin.webp'
import { Link } from 'react-router-dom';

const Footer = () => {
  return (
    <footer className="footer">
      <div className="copyright">
        <span>Copyright &copy; 2022 The Youth Magnate. All Rights Reserved.</span>
      </div>
      <div className="icons">
      <a href="https://www.instagram.com/theyouthmagnate/">
          {/* <i class="fab fa-instagram-square fa-4x inst"></i> */}
          <img src={InstaIcon} alt="Insta" className="fab" />
        </a>
        <a href="https://www.facebook.com/Theyouthmagnate/">
          {/* <i class="fab fa-facebook-square fa-4x fb"></i> */}
          <img src={FacebookIcon} alt="" className="fab" />
        </a>
        <a href="https://in.linkedin.com/company/the-youth-magnate">
          {/* <i class="fab fa-twitter-square fa-4x twit"></i> */}
          <img src={LinkedinIcon} alt="" className="fab" />
        </a>
        
        <a href="https://www.youtube.com/channel/UCzW0j8hXjNe7jfjAd8wLlgw" className="yout">
          {/* <i class="fab fa-instagram-square fa-4x inst"></i> */}
          <img src={YoutubeIcon} alt="Youtube" className="fab" />
        </a>
      </div>
    </footer>
  );
};
export default Footer;
