import React from "react";
import { Link } from "react-router-dom";
import Logo from "./_logo.jpg";
import "./header.styles.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Logowriting from "./TYM written.png";

class Header extends React.Component {
  constructor() {
    super();
    this.state = {
      rememberme: false,
    };
  }
  handleHover(e) {
    if (e.target.classList.contains("nav__link-header")) {
      const link = e.target;
      const siblings = link.closest(".nav-header").querySelectorAll(".nav__link-header");
      // const logo = link.closest(".nav").querySelector("img");
      // const logo2 = link.closest(".nav").querySelector("img2");

      siblings.forEach((el) => {
        if (el !== link) el.style.opacity = this;
      });
      // logo.style.opacity = this;
      // logo2.style.opacity = this;
    }
  }
  menuOpen() {
    const overlay = document.querySelector(".overlay");
    overlay.classList.add("overlay--active");
  }
  menuClose() {
    const overlay = document.querySelector(".overlay");
    overlay.classList.remove("overlay--active");
  }
  render() {
    return (
      <div className="header-header">
        <header
          className="nav-header"
          onMouseOver={this.handleHover.bind(0.3)}
          onMouseOut={this.handleHover.bind(1)}
        >
          <a class="logo-header" href="/">
            <Link to="/home" className="LogoLink-header">
              <img src={Logo} alt="logo" className="Logo-header img-header" />
              <img src={Logowriting} alt="" className="tym-header img2-header" />
            </Link>
          </a>
          <nav>
            <ul class="nav__links-header">
              <li>
                <Link to="/home">
                  <a href="#" className="nav__link-header">
                    Home
                  </a>
                </Link>
              </li>
              <li>
                <Link to="/about-us">
                  <a href="#" className="nav__link-header">
                    About us
                  </a>
                </Link>
              </li>
              <li>
                <Link to="/blog">
                  <a href="#" className="nav__link-header">
                    Blog
                  </a>
                </Link>
              </li>
              <li>
                <Link to="/contact-us">
                  {/* <a href="#" className="nav__link nav__link--btn"> */}
                  <a href="#" className="nav__link-header">
                    Contact Us
                  </a>
                </Link>
              </li>
              {/* <li>
                <Link>
                <a href="#">Contact</a>
                </Link>
              </li> */}
            </ul>
          </nav>
          <p class="menu cta" onClick={this.menuOpen}>
          <div class="hamburger-lines">
              <span class="line line1">__</span>
              <span class="line line2">__</span>
              <span class="line line3">__</span>
            </div>  
          </p>
        </header>
        <div id="mobile__menu" class="overlay">
          <a class="close" onClick={this.menuClose}>
            &times;
          </a>
          <div
            class="overlay__content nav-header"
            onMouseOver={this.handleHover.bind(0.5)}
            onMouseOut={this.handleHover.bind(1)}
          >
            <Link to="/home">
              <a onClick={this.menuClose} className="nav__link-header">
                Home
              </a>
            </Link>
            <Link to="/about-us">
              <a onClick={this.menuClose} className="nav__link-header">
                About Us
              </a>
            </Link>
            <Link to="/blog">
              <a onClick={this.menuClose} className="nav__link-header">
                Blog
              </a>
            </Link>
            <Link to="/contact-us">
              <a onClick={this.menuClose} className="nav__link-header">
                Contact Us
              </a>
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;
