import React from "react";
import { Link } from "react-router-dom";
import "./about-us.styles.scss";

/// Pitcures
import FoundPic from "./Pitcures/Anmol and Soumya Picture.jpg";
import Anjali from "./Pitcures/Anjali.jpg";
import Swasti from "./Pitcures/Swasti.jpg";
import Aman from "./Pitcures/Aman.jpg";
import Satwik from "./Pitcures/Satwik.jpg";
import Tanya from "./Pitcures/Tanya.jpg";
import Thea from "./Pitcures/Thea.JPG";
import Gunkruti from "./Pitcures/Gunkruti.jpg";
import Nethra from "./Pitcures/Nethra.jpg";
import kshirija from "./Pitcures/kshirija.jpg";
import Mohadisa from "./Pitcures/Mohadisa.jpg";
import Aishani from "./Pitcures/Aishani.jpg";
import Avni from "./Pitcures/Avni.jpeg";
import Tripti from "./Pitcures/Tripti.jpeg";

import Header from "../../components/header/header.component";
import Footer from '../../components/footer-2/footer.component'

const AboutUsPage = () => {
  return (
    <div>
      <div className="about-us">
        <div className="about-us-1page">
          <Header />
          <h1>
            <b>ABOUT THE FOUNDERS</b>
          </h1>
          <div className="founders-about">
            <div className="image-container-founders">
            <img src={FoundPic} alt="" />
            </div>
            <div className="content-about-us-all">
              <h2>Anmol Chawla and Soumya Sinha</h2>
              <p>
                Growing up together since the age of 3, we were always driven
                with a strong sense of purpose to start an initiative together.
                Interviewing people to learn about their journey through
                hardships, struggles and wins was something that was truly
                intriguing and motivating to us! After looking for stories that
                were inspiring and relatable to our generation, we realised that
                such stories about today’s youth is fairly unconventional. That
                is what lead us to create 'The Youth Magnate' in 2021. More than
                a year later, we have built a platform to present and showcase
                stories of the youth from different parts of the world as we
                truly believe, 'Inspiration is everywhere, we just need to find
                it!'
              </p>
            </div>
          </div>
        </div>
        <div className="about-us-2page">
          <h1>
            <b>RESEARCHERS</b>
          </h1>
          <div className="researchers">
            <div className="researchers-about">
              <div className="image-container-researchers">
                <img src={Anjali} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>ANJALI MEHTA</h2>
                <p>
                  Anjali is a voracious reader, whose interests span from baking
                  to the hit TV show Friends. Always up for a midnight marvel
                  movies marathon or anything else related to star wars. She
                  loves all animals, though in all honesty dogs hold a special
                  place in her heart. She wishes to travel all 7 continents in
                  her lifetime, and learn about cultures across the world. Her
                  motto is work hard and play hard!
                </p>
              </div>
            </div>
            <div className=" researchers-about">
              <div className="image-container-researchers">
                <img src={Swasti} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>SWASTI SHARMA</h2>
                <p>
                  Swasti is an amiable and a passionate person who puts in her
                  heart and soul towards everything she does! Her maturity and
                  sensibility is a characteristic that everyone around her makes
                  sure to acknowledge. With an undeniable love for the famous
                  sitcom, 'Friends', She is the perfect person to binge watch
                  all the episodes with. An adored researcher of our team, she
                  never fails to disappoint!
                </p>
              </div>
            </div>
            <div className="researchers-about">
              <div className="image-container-researchers">
                <img src={Aman} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>AMAN JAIN</h2>
                <p>
                  Aman is an enthusiast who loves directing his energy in
                  protecting the environment and trying to bring a change in our
                  world. He finds it easier to express himself in words and
                  often finds himself weaving his thoughts into comforting
                  blankets of poems. He loves to care and nurture for himself
                  just as he loves to look after his plants. He loves listening
                  to music and cycling. He is a proud plantholic and loves his
                  internship as a researcher here.
                </p>
              </div>
            </div>
            <div className="researchers-about">
              <div className="image-container-researchers">
                <img src={Satwik} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>SATWIK SHARMA</h2>
                <p>
                  Satwik is a sports lover who loves to invest his time in
                  adrenaline boosting activities like basketball and football.
                  He is a benevolent individual who will go out of his way to
                  help everyone. His life mantra is honesty is the best policy
                  which makes him an awe inspiring team member. He is an
                  industrious individual who is always ready to take up any new
                  challenges. His passion towards his work is absolutely
                  remarkable!
                </p>
              </div>
            </div>
          </div>
        </div>
        <div className="about-us-3page">
          <h1>
            <b>CONTENT WRITERS</b>
          </h1>
          <div className="content-writers">
            <div className="content-writers-about">
              <div className="image-container-content-writers">
                <img src={Tanya} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>TANYA KATRE</h2>
                <p>
                  On an average Sunday morning you'd find her curled up in her
                  room, binge read a Young Adult romance novel or watching a
                  murder documentary without a flinch. Our content writer Tanya,
                  is passionate, determined and spontaneous. With a great taste
                  in vocabulary and an even greater zeal for writing she hopes
                  to bring forward impactful and meaningful stories under The
                  Youth Magnate.
                </p>
              </div>
            </div>
            <div className="content-writers-about">
              <div className="image-container-content-writers">
                <img src={Thea} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>THEA SHAH</h2>
                <p>
                  Someone who's forever engrossed with K3G, is an orchestrated
                  chaos, mother of a plant and defender of the ones who got her
                  back, is one of our adored Content Writers here at The Youth
                  Magnate, Thea. She is someone who never forgets any story you
                  tell her, always remembers the tiniest of details, and every
                  time you need her, she'll be right there. She's also a
                  self-critical poet who scribbles down a lot but never posts
                  anywhere, is always down to teach you garba, and if you wanna
                  get a movie-like dreamy sequence of her intro, just picture a
                  girl rummaging in heels in the funniest of situations,
                  tripping over things and fumbling over her own sentences
                  because her thoughts rush faster than her words.
                </p>
              </div>
            </div>
            <div className="content-writers-about">
              <div className="image-container-content-writers">
                <img src={Gunkruti} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>GUNKRUTI KAUR</h2>
                <p>
                  Someone who just can’t let the ideas inside her mind be
                  stored, she has to implement them, so keen to know why the
                  world can’t just be a better place. She is a fascinating girl
                  who likes to write poetry and can be mischievous when writing
                  since you never know what she may be able to do. She is very
                  punctual and ambitious about her goals. You will always find
                  her to be upskilled with all the latest trends and skills in
                  the world!
                </p>
              </div>
            </div>
            <div className="content-writers-about">
              <div className="image-container-content-writers">
                <img src={Nethra} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>NETHRA VENKATRAMAN</h2>
                <p>
                  Nethra’s passion for writing is evident in her every way of
                  life. She is a bardolater who is always up for a Shakespeare
                  fan girl session. She is a self made writer who started her
                  writing journey with random prompts and she ended up falling in love with article
                  writing. She is an extrovert who loves interacting with
                  strangers and makes them feel comfortable and welcomed. Thus,
                  Interviewing people is her favourite part about being a member
                  of the team and she always looks forward to it.
                </p>
              </div>
            </div>
            <div className="content-writers-about">
              <div className="image-container-content-writers">
                <img src={kshirija} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>KSHIRIJA ARORA</h2>
                <p>
                  Generally a fun loving person with a witty personality, people
                  call her when they want a laugh. Perfection is something that
                  comes naturally to her making her the most dependable person.
                  Poetry runs through her blood as she is a natural poet and
                  claims to have a little piece of herself in everything that
                  she has ever written. With Chidi from the Good Place as her
                  spirit animal she looks forward to making new friends in life.
                  An ambitious person with high aspirations, she works hard
                  every single day to achieve them.
                </p>
              </div>
            </div>
            <div className="content-writers-about">
              <div className="image-container-content-writers">
                <img src={Mohadisa} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>MOHADISA RIZVI</h2>
                <p>
                  Mohadisa is a very passionate and charming girl, the kind who
                  puts her full mind and soul into the work she does. She is
                  someone who looks forward to her life with a bright smile on
                  her face, someone with whom you can never feel left out. She
                  will listen to all your problems without being judgemental and
                  will keep your secrets like a treasure. Being considerate is
                  something she does daily. She is the kind of person who likes
                  everything to be in its place. Yes, she is a "Neatnick"
                </p>
              </div>
            </div>
            <div className="content-writers-about">
              <div className="image-container-content-writers">
                <img src={Aishani} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>AISHANI MEHRA</h2>
                <p>
                  Aishani is the friend you can always take your problems to. An
                  avid reader with a special interest in mystery, she loves
                  solving puzzles and putting together clues! Obsessed with
                  listening to Frank Ocean and Lana Del Ray, you'll be
                  guaranteed to find her with earphones in at any time of the
                  day. With an intense passion for travelling, exploring and
                  delving deep into other cultures, she loves the spirit of
                  adventure!
                </p>
              </div>
            </div>
          </div>
        </div>
        <div className="about-us-4page">
          <h1>
            <b>MARKETING TEAM</b>
          </h1>
          <div className="marketing-team">
            <div className="marketing-team-about">
              <div className="image-container-marketing-team">
                <img src={Avni} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>AVNI AGRAWAL</h2>
                <p>
                  Avni is an extremely outgoing and empathetic person. She might
                  occasionally be overdressed but that’s just her undying love
                  for fashion. She considers Shawn Mendes songs to be free
                  therapy and would definitely tell you random facts about him.
                  No matter what she is always up for naps. Her creative ideas
                  are without doubt her best attribute.
                </p>
              </div>
            </div>
            <div className="marketing-team-about">
              <div className="image-container-marketing-team">
                <img src={Tripti} alt="" />
              </div>
              <div className="content-about-us-all">
                <h2>TRIPTI LADHA</h2>
                <p>
                  Tripti's effervescent personality makes her quite endearing.
                  Her compassionate and caring nature fuels her engagement in
                  social welfare causes. Being an avid sportsperson has nurtured
                  her teamwork and leadership skills. She loves to dance, paint
                  and spend her spare time writing poetry. As a patient
                  listener, Tripti is always a call away!
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  );
};
export default AboutUsPage;
