// Dr Ritika Parek -
// Every day when our minds would replay what our heart couldn’t delete, we all would motivate ourselves in the hope to save one more life and Thus, put on a big smile under the mask which held more pain than tears could ever have. We realised that time is the most precious in this materialistic world and we learnt that sometimes little things in life are more than enough.Once an elderly patient was diagnosed with covid and was skeptical about admission in the ICU. His hesitation caught my friend's attention, who was able to convince him to get admitted . She would video call him every day and keep him motivated; thank his optimism and the medical team that he got cured.Finding joy from such serene moments of patients discharging after being cured, and our families support, we bet our way through these horrendous times. Sometimes the memories of the hard times, of having to disclose the bad news to the patients' families still roll down my cheeks. But, the number of people we were able to help makes it all worth the sadness we encountered.” Says, Dr. Ritika Parekh.
// Abhidaan -
// As tenth graders, performing, earning, and doing more than just the usual, was exciting. However, it went downhill when covid hit. The number of gigs all shot down, but Abhidaan didn't collapse. We kept posting covers, trying to make the most of our time at home. With some improvements in the global situation and our shift to a new city, Abhidaan now has space to flourish more than ever! This year, we are looking forward to making and putting out more music. It's just the beginning for us, but we are grateful for anybody who acknowledges our originals, recognises us in random places like the gym and loves the ban Abhidaan is about two best friends, musicians and people who love what they do: working together, helping each other be better, and just having fun finding their way around this world of music!” Say Abhimanyu and Vardaan
// Prem Ningobam -
// My family has been very supportive throughout and were thrilled when they saw the suit, but the locals in my neighborhood only recognized me as an artist. They had no idea about my recent project. When my video went viral, everyone was perplexed but delighted at the same time! I'd love to sell these suits and employ the unemployed along with producing something beneficial for the entire human race! If Marvel ever reaches out to me, it would be the happiest day of my life!” Says Prem Ningombam
// Navya and Nadia -
//  Even raising sixteen thousand rupees was a huge deal! Initially, We were not sure if people would donate to this cause. We even heard comments like “Agar help karni hi hai, to apno ki karo” but to our surprise, We ended up raising Rs 40000 which helped us cater 184 refugees! We received donations not only from India but USA ,Canada and Saudi Arabia as well. We arranged blankets with the help of a local vendor and along with a few more volunteers, The drive went on smoothly! While we were wrapping up, A group of women refugees called us in again and handed us a beautiful pair of earrings made by them. That small gesture touched our hearts and is a gift that we will always keep with us! This was just a small project but I don't think we would ever like to stop. Even if a small microscopic effort makes an impact, We are always there to help!”
